angular
  .module('app', ['ui.router','ngMaterial']);

angular
  .module('app')
  .component('leftFloatingMenu', {
    templateUrl: 'app/components/left-floating-menu/left-floating-menu.html',
    controller: LeftFloatingMenuController
  });

/** @ngInject */
function LeftFloatingMenuController($scope) {
  var vm = this;
  
  vm.close = function(){
      $scope.$emit('close-left-floating-panel');
  };
  
}

angular
  .module('app')
  .component('fountainHeader', {
    templateUrl: 'app/components/header/header.html'
  });

angular
  .module('app')
  .component('fountainFooter', {
    templateUrl: 'app/components/footer/footer.html'
  });

angular
  .module('app')
  .component('app', {
    templateUrl: 'app/main.html',
    controller: AppController
  });

  function AppController($scope, $timeout, $mdSidenav, $log){
    $scope.toggleLeft = buildDelayedToggler('left');

    /**
     * Supplies a function that will continue to operate until the
     * time is up.
     */
    function debounce(func, wait, context) {
      var timer;
      return function debounced() {
        var context = $scope,
            args = Array.prototype.slice.call(arguments);
        $timeout.cancel(timer);
        timer = $timeout(function() {
          timer = undefined;
          func.apply(context, args);
        }, wait || 10);
      };
    }
    /**
     * Build handler to open/close a SideNav; when animation finishes
     * report completion in console
     */
    function buildDelayedToggler(navID) {
      return debounce(function() {
        // Component lookup should always be available since we are not using `ng-if`
        $mdSidenav(navID)
          .toggle()
          .then(function () {
            $log.debug("toggle " + navID + " is done");
          });
      }, 200);
    }

    $scope.$on('close-left-floating-panel', function(evt, data){
        $mdSidenav('left').close()
          .then(function () {
          $log.debug("close LEFT is done");
        });
    });
  }

